

























































import Vue from "vue";
import Component from "vue-class-component";

import { XHelper } from "@/x-vue-helper/x-helper";
import { AppService } from "@/service/app.service";
import { ApiService } from "@/x-vue/services/api.service";
import { RequestData } from "@/x-vue/interfaces/interfaces";
import { CommentModel, PostModel } from "@/x-vue/interfaces/forum.interface";

import CommentForm from "@/x-vue/components/forum/comment/CommentForm.vue";
import PostMeta from "@/x-vue/components/forum/post/PostMeta.vue";
import CommentMeta from "@/x-vue/components/forum/comment/CommentMeta.vue";
import VoteButtons from "@/x-vue/components/forum/VoteButtons.vue";
import MineButtons from "@/x-vue/components/forum/MineButtons.vue";
import UserDisplayName from "@/x-vue/components/forum/UserDisplayName.vue";
import UserAvatar from "@/x-vue/components/forum/UserAvatar.vue";
import FileList from "@/x-vue/components/file/FileList.vue";
import Content from "@/x-vue/components/forum/Content.vue";

@Component({
  components: {
    CommentForm,
    CommentMeta,
    PostMeta,
    VoteButtons,
    MineButtons,
    UserDisplayName,
    UserAvatar,
    FileList,
    Content,
  },
}) // without this, Vue lifecycle methods will not be invoked
export default class PostView extends Vue {
  api: ApiService = ApiService.instance;
  app: AppService = AppService.instance;
  post: PostModel = new PostModel();
  loading = false;

  async mounted(): Promise<void> {
    // console.log("PostView::mounted::");
    this.loading = true;

    const arr = location.href.split("/");
    const last = arr[arr.length - 1];
    const options: RequestData = {
      idx: last,
    };

    try {
      this.post = await this.api.postGet(options);
      this.$store.commit("currentCategory", this.post.categoryId);
      this.loading = false;
    } catch (e) {
      this.loading = false;
      this.$app.error(e);
    }
  }

  get toListPage(): string {
    return "forum/" + this.post.categoryId + location.search;
  }

  onVoted(): void {
    this.app.refreshProfile();
  }

  onCommentEdited(): void {
    // console.log("commentEdited::comment", comment);
    this.app.refreshProfile();
  }

  onClickEdit(entity: PostModel & CommentModel): void {
    // console.log("onClickEdit", entity);
    if (entity.isPost) {
      this.app.open(`/forum/edit/${entity.idx}` + location.search);
    } else {
      entity.inEdit = true;
    }
  }

  async onClickDelete(entity: PostModel & CommentModel): Promise<void> {
    // console.log("onClickDelete", entity);

    const conf = await XHelper.instance.confirm(
      "Title",
      `Are you sure you want to delete this ${entity.isPost ? "post" : "comment"}`
    );
    if (!conf) return;
    try {
      let res;
      if (entity.isPost) {
        res = await this.api.postDelete(entity.idx);
        this.app.open(this.toListPage);
      } else {
        res = await this.api.commentDelete(entity.idx);
      }
      entity = Object.assign(entity, res);
    } catch (e) {
      XHelper.instance.error(e);
    }
    this.app.refreshProfile();
  }
}
